import moment from 'moment';

export const setDay = day => {
  moment.updateLocale('en', { week: { dow: 1 } });
  let currentDay;
  if (!day) {
    currentDay = moment();
  } else {
    currentDay = moment(day).clone();
  }
  const startMonth = currentDay.clone().startOf('month'); //   
  const endMonth = currentDay.clone().endOf('month'); //   

  const startDay = startMonth.clone().startOf('week'); //   ,     
  const endDay = endMonth.clone().endOf('week'); //   ,     

  const startWeek = currentDay.clone().startOf('week'); //   ,    
  const endWeek = currentDay.clone().endOf('week'); //    ,    

  const dayDifference = endDay.diff(startDay, 'days') + 1; //  ,     ()
  const weeks = dayDifference / 7; //    ()

  const startCalendar = startDay.clone().subtract(1, 'day');
  const daysArray = [...Array(dayDifference)].map(() =>
    startCalendar.add(1, 'day').clone()
  );

  const dates = {
    startMonth,
    endMonth,
    startDay,
    endDay,
    startWeek,
    endWeek,
    dayDifference,
    weeks,
    daysArray,
  };
  return dates;
};
